import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';
import { IPig, Gender } from './pig.model';

// export interface
@Injectable({
  providedIn: 'root',
})
export class PigsService {
  private pigs: IPig[] = [
    { id: '112', birth: new Date(), gender: Gender.female },
    { id: '2', birth: new Date(), gender: Gender.male },
    { id: '3', birth: new Date(), gender: Gender.female },
    { id: '4', birth: new Date(), gender: Gender.female },
    { id: '5', birth: new Date(), gender: Gender.female },
  ];

  getPigs(): Observable<IPig[]> {
    return of(this.pigs);
  }

  getPig(id: string): Observable<IPig> {
    const pig = this.pigs.find((p) => p.id === id);
    return pig ? of(pig) : throwError('404');
  }
}
